* {
  font-family: "Nanum Gothic", sans-serif;
  margin: 0px;
  padding: 0px;
  }
  
  html {
  scroll-behavior: smooth;
  }
  
  .navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: lightblue;
  position: fixed;
  top: 0;
  }
  
  .navbar-name {
  font-size: 28px;
  margin: 0px 30px;
  padding: 10px;
  }
  
  .navbar ul * {
  margin-right: 30px;
  }
  
  .app section {
  border: 1px solid black;
  background-color: rgb(10, 10, 54);
  }
  
  /* about */
  
  .section-parent {
  margin: 60px;
  display: flex;
  flex-direction: column;
  background-color:gray;
  }
  
  .section-parent h1, p {
  padding: 20px;
  }
  
  
  /* about */
  
  .about-content {
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color:lightgray;
  }
  
  
  #headshot {
  max-width: 100%;
  max-height: 400px;
  border-radius: 50%;
  }
  
  .about-description {
  margin-left: 5px;
  padding: 5px;
  background-color: lightcoral;
  }
  
  
  /* Footer */
  #footer {
  background-color: lightgray;
  }
  
  #footer img {
  max-width: 40px;
  max-height: 30px;
  }
  
  #footer-container {
  padding-top: 25px;
  padding-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-direction: row;
  background-color: lightgray;
  justify-content: space-around;
  max-width: 300px;
  }
  
  /* Projects */
  #projects div h1 {
  background-color: aquamarine;
  }
  
  .project-content {
  padding: 5px;
  display: flex;
  flex-direction: row;
  background-color: yellow;
  }
  
  #project-photo {
    max-width: 40px;
    max-height: 40px;
    max-width: 100%;
    max-height: 400px;

  }
  
  .project {
  margin: 4px;
  width: 100%;
  background-color: lightblue;
  display: flex;
  flex-direction: column;
  }
  
  .project h1 {
  text-align: center;
  }
  
  .project-links {
  margin: 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  }
  .project-link {
    text-decoration: none;
    color: inherit;
  width: 100%;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: white;
  margin: 2px;
  border-radius: 3px;
  }
  
  .project-description {
  padding: 5px;
  background-color: orange;
  }